import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MdDialog } from '@angular/material';

// Entity
import { TermTab } from '../../class/term-tab/term-tab';

import { LodingDialogComponent } from '../../component/loding-dialog/loding-dialog.component';

// API
import { ApiAccessorService } from '../../service/api/api-accessor.service';

/**
 * 銘柄コードの最大文字数
 */
const MAX_BRAND_CODE_LENGTH = 4;

// === 期間タブ ===
/**
 * 5分足
 */
const FIVE_MIN_ID : string = 'five_min';

/**
 * 1日足
 */
const ONE_DAY_ID : string = 'one_day';

/**
 * 1ヶ月足
 */
const ONE_MON_ID : string = 'one_month';

/**
 * 検索中
 */
const SEARCHING_ON : boolean = true;

/**
 * 検索終了
 */
const SEARCHING_OFF : boolean = false;

@Component({
	selector: 'app-brand',
	templateUrl: './brand.component.html',
	styleUrls: ['./brand.component.css']
})
export class BrandComponent implements OnInit, OnDestroy {
	/**
	 * 初回銘柄コード
	 * @type {string}
	 */
	private code : string;

	/**
	 * 期間タブ
	 * @type {Array<TermTab>}
	 */
	private termTabs : Array<TermTab> = [
		new TermTab(FIVE_MIN_ID, '5分足'),
		new TermTab(ONE_DAY_ID, '1日足'),
		new TermTab(ONE_MON_ID, '1ヶ月')
	];

	/**
	 * 選択している期間
	 */
	private selectedTerm : string;

	/**
	 *  チャート用表示データ
	 */
	private chartData : Object;

	/**
	 * 検索ステータス
	 */
	private searchState : boolean = SEARCHING_OFF;

	/**
	 * @constructor
	 */
	constructor(
		private router : ActivatedRoute,
		private api : ApiAccessorService,
		private dialog : MdDialog
	) {}

	/**
	 * 初期化処理
	 */
	ngOnInit() : void {
		console.log('OnInit');
		// チャートデータの初期化
		this.chartData = null;

		// 初期表示では､5分足を選択しているものとする｡
		this.selectedTerm = FIVE_MIN_ID;

		// クエリパラメータの取得
		this.router.queryParams.subscribe((params) => {
			// クエリパラメータがある場合
			if (params.code) {
				// クエリパラメータの値を設定する｡
				this.code = params.code;
				// 銘柄コードの情報を取得する｡
				this.getBrandInfo(this.code, this.selectedTerm);
			} else {
				// クエリパラメータがない場合
				this.code = null;
			}
		});
	}

	ngOnDestroy() : void {
		console.log('OnDestroy');
		this.chartData = null;
	 }

	/**
	 * 銘柄情報取得処理
	 * @param code {string} 銘柄コード
	 * @param term {string} 期間
	 */
	 private getBrandInfo(code : string, term : string) : void {
		// ダイアログを表示する
		this.dialog.open(LodingDialogComponent);

		this.api.getBrandInfoAccessorService().getBrandInfo()
		.subscribe(
			// Success
			(result) => {
				// 表示データを作成する
				this.chartData = this.createChartData(result);
			},
			// Fail
			(error) => {
				console.error(error);
			},
			// Complate
			() => {
				console.log('complate');
				// ダイアログを閉じる
				this.dialog.closeAll();
			}
		);
	 }

	/**
	 * チャート用表示データ作成処理
	 * @param data {Object} 株価データ
	 * @return {any} チャート用データ
	 */
	 private createChartData(data : Object) : Object {
		// チャートデータ
		let chartData = {
			'chartType': 'LineChart',
			'dataTable': [
				['日付', '終値']
			],
			'options': {'銘柄名': data['name']}
		};

		// 取得したデータをチャート表示用に加工する｡
		for (let stock of data['stockData']) {
			let param = [
				stock['label'],
				stock['close']
			];
			chartData['dataTable'].push(param);
		}

		return chartData;
	 }

	/**
	 * 銘柄コード入力フォーム キーダウンイベント
	 * @param brandCodeObj {Object} 銘柄コード入力フォームオブジェクト
	 */
	private onChangeCode() : void {
		if (this.code.length > MAX_BRAND_CODE_LENGTH) {
			this.code = this.code.slice(0, MAX_BRAND_CODE_LENGTH);
		}
	}

	/**
	 * 検索ボタン 押下イベント
	 * @param code {string} 銘柄コード
	 */
	private onClickSearchButton(code) : void {
		// 銘柄コードの情報を取得する｡
		this.getBrandInfo(code, this.selectedTerm);
	}

	/**
	 * 期間タブ チェンジイベント
	 * @param tab {Object} タブオブジェクト
	 */
	private onChangeTab(tab) : void {
		// 選択した期間を保持
		this.selectedTerm = this.termTabs[tab.index]['id'];
		// 検索を実行する
		this.getBrandInfo(this.code, this.selectedTerm);
	}
}



