import { Component, OnInit } from '@angular/core';
import { ApiAccessorService } from '../../service/api/api-accessor.service';

import 'rxjs/Rx';

@Component({
	selector: 'app-portfolio',
	templateUrl: './portfolio.component.html',
	styleUrls: ['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {
	/**
	 * ポートフォリオリスト
	 * @private
	 * @type {Array<any>}
	 */
	private portfolioList : Array<any> = [];

	/**
	 * @constructor
	 * @param api {ApiAccessorService}
	 */
	constructor(private api : ApiAccessorService) { }

	/**
	 * 初期化処理
	 */
	ngOnInit() : void {
		let portfolioAccessor = this.api.getPortfolioAccessorService();

		portfolioAccessor.queryPortfolio().subscribe((result) => {
			this.portfolioList = result;
		});
	}


}



