import { Component, OnInit } from '@angular/core';

// === Entity ===
import { MenuEntity } from '../../class/menu/menu-entity';

@Component({
	selector: 'app-home',
	templateUrl: './home.component.html',
	styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

	/**
	 * メニューリスト
	 * @private
	 * @type {Array<MenuEntity>}
	 */
	private menuList : Array<MenuEntity> = [
		new MenuEntity('HOME', ''),
		new MenuEntity('PORTFOLIO', 'portfolio'),
		new MenuEntity('BRAND', 'brand'),
		new MenuEntity('PROFIT', 'profit')
	];

	/**
	 * @constructor
	 */
	constructor() { }

	/**
	 * 初期化処理
	 */
	ngOnInit() {
	}

	/**
	 * メニューアイコンクリックインベント
	 * @private
	 * @method
	 * @param sidenav {Object} サイドナビゲーション
	 */
	private onClickMenuIcon(sidenav) : void {
		( sidenav.opened ) ? sidenav.close() : sidenav.open();
	}
	/**
	 * メニュークリックイベント
	 * @private
	 * @method
	 * @param sidenav {Object} サイドナビゲーション
	 * @param menu {Object} 選択したメニュー
	 */
	private onClickMenu(sidenav, menu) : void {
		// サイドバーをとじる
		sidenav.close();
	}

	/**
	 * 画面表示メニュー 確認処理
	 * @param menu { MenuEntity } メニュー
	 */
	private checkDisplay(menu : MenuEntity) : boolean {
		// 今開いている画面の場合､非活性にする｡
		let href = window.location.href;

		// ホームの場合はfalseを強制する
		if (menu.url === '') {
			return false;
		}

		return ( href.indexOf(menu.url) !== -1 );
	}
}



