import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable';

// 各API
import { PortfolioAccessorService } from './portfolio/portfolio-accessor.service';
import { BrandInfoAccessorService } from './brand-info/brand-info-accessor.service';

/**
 * @class { ApiAccessorService }
 * @export
 */
@Injectable()
export class ApiAccessorService {

	/**
	 * @constructor
	 */
	constructor() { }

	/**
	 * ポートフォリオAPI 取得処理
	 * @return { PortfolioAccessorService }
	 */
	public getPortfolioAccessorService() : PortfolioAccessorService {
		return new PortfolioAccessorService();
	}

	/**
	 * 銘柄情報API 取得処理
	 */
	public getBrandInfoAccessorService() : BrandInfoAccessorService {
		return new BrandInfoAccessorService();
	}
}



