/**
 * @class TermTab 期間タブ
 */
export class TermTab {
	/**
	 * ID
	 * @type {string}
	 */
	private id : string;


	/**
	 * ラベル
	 * @type {string}
	 */
	private label : string;

	/**
	 * 
	 * @param id {string} 期間タブのID
	 * @param label {string} 期間タブのラベル
	 */
	constructor(id, label) {
		this.id = id;
		this.label = label;
	}
}



